.news-compact {
  margin: 0 0 var(--paragraph-margin-bottom);
}
@media (min-width: 576px) {
  .news-compact {
    max-width: var(--container-sm-width);
  }
}
@media (min-width: 768px) {
  .news-compact {
    max-width: var(--container-md-width);
  }
}
@media (min-width: 992px) {
  .news-compact {
    max-width: var(--container-lg-width);
    margin-left: calc((var(--container-lg-width) - var(--container-md-width)) / -2);
    margin-right: calc((var(--container-lg-width) - var(--container-md-width)) / -2);
  }
}
@media (min-width: 1200px) {
  .news-compact {
    max-width: var(--container-xl-width);
    margin-left: calc((var(--container-xl-width) - var(--container-md-width)) / -2);
    margin-right: calc((var(--container-xl-width) - var(--container-md-width)) / -2);
  }
}
.news-compact > header {
  background: var(--black);
  box-shadow: var(--box-shadow);
  color: rgba(var(--primary-rgb), 0.875);
  margin: var(--paragraph-margin-bottom) auto;
  padding: var(--paragraph-margin-bottom);
  text-align: center;
}
@media (min-width: 768px) {
  .news-compact > header {
    margin-left: calc(var(--paragraph-margin-bottom) / -4);
    margin-right: calc(var(--paragraph-margin-bottom) / -4);
  }
}
@media (min-width: 992px) {
  .news-compact > header {
    margin: var(--paragraph-margin-bottom) auto;
    max-width: calc(var(--container-md-width) - var(--paragraph-margin-bottom));
  }
}
.news-compact > header h2 {
  color: rgba(var(--primary-rgb), 0.875);
  margin-bottom: 0;
}
.news-compact > header p {
  font-size: calc(var(--base-font-size) * 1.125);
  line-height: 1.25;
}
.news-compact > header a {
  border-bottom: 2px solid rgba(var(--primary-rgb), 0.25);
  color: var(--primary);
}
.news-compact > header a:hover {
  border-bottom: 2px solid rgba(var(--primary-rgb), 0.75);
  color: var(--primary);
}
@media (min-width: 768px) {
  .news-compact article.has-featured-image {
    align-items: center;
    display: grid;
    grid-column-gap: var(--grid-gap);
    grid-row-gap: calc(var(--grid-gap) * 0.5);
    grid-template-areas: "header header" "image content";
    grid-template-columns: repeat(2, 50fr);
  }
}
@media (min-width: 992px) {
  .news-compact article.has-featured-image {
    grid-template-columns: 40fr 60fr;
  }
}
@media (min-width: 1200px) {
  .news-compact article.has-featured-image {
    grid-template-columns: repeat(2, 50fr);
  }
}
.news-compact article.has-featured-image .post-thumbnail {
  align-self: flex-start;
  grid-area: image;
  margin: calc(var(--paragraph-margin-bottom) * 0.5) 0;
}
@media (min-width: 768px) {
  .news-compact article.has-featured-image .post-thumbnail {
    margin: calc(var(--paragraph-margin-bottom) * 0.5) 0 0;
  }
}
.news-compact article.has-featured-image .article-content {
  grid-area: content;
}
.news-compact .article-header {
  background: transparent;
  box-shadow: none;
  grid-area: header;
  margin: 0 auto !important;
  max-width: var(--container-md-width);
  padding: 0;
}
.news-compact .article-header h3 {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .news-compact .article-content {
    font-size: calc(var(--base-font-size) * .875);
  }
}
.news-compact article:not(.has-featured-image) {
  margin: 0 auto;
  max-width: calc(var(--container-md-width) - var(--paragraph-margin-bottom));
}

.news-more {
  padding-bottom: var(--paragraph-margin-bottom);
  text-align: center;
}